<template>
  <div id="app">
    <h1 class="mt-4 text-center">Form using vue</h1>
            <form>
              <div class="form-group">
                <label class="lebel1"><b>Enter Employee ID :</b></label>
                <input type="number" class="input1" v-model="empid" />
                </div>
              <div class="form-group">
                <label for="fname"><b>First Name :</b></label>
                <input
                  type="text"
                  v-model="fname"
                  class="form-control"
                />
              </div>
              <div class="form-group">
                <label for="lname"><b>Last Name :</b></label>
                <input
                  type="text"
                  v-model="lname"
                  class="form-control"
                />
              </div>
              <div class="form-group">
                <label class="lebel1"><b>Enter age :</b></label>
                <input type="number" min="1" max="80" class="input1" v-model="age"/>
                </div>
                <div class="form-group">
                <label class="lebel1"><b>Enter gender :</b></label>
                <input type="text" class="input1" v-model="gender"/>
                </div>
              <button type="button" @click="onSubmit" class="btn btn-dark">
                Submit
              </button>
            </form>
            <table class="table mt-5">
              <thead>
                <tr>
                  <!-- <th scope="col">#</th> -->
                  <th scope="col"> Emp. ID</th>
                  <th scope="col">First Name</th>
                  <th scope="col">Last Name</th>
                  <th scope="col"> Age </th>
                  <th scope="col"> Gender </th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(entry, i) in sortedList" :key="i">
                  <!-- <th scope="row">{{ ++i }}</th> -->
                  <td>{{ entry.empid }}</td>
                  <td>{{ entry.fname }}</td>
                  <td>{{ entry.lname }}</td>
                  <td>{{ entry.age }}</td>
                  <td>{{ entry.gender }}</td>
                </tr>
              </tbody>
            </table>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      empid: "", 
      fname: "", 
      lname: "", 
      age: "", 
      gender: "" , 
      allScores: [] 
    }
  },
  computed: {
    sortedList: function() {
      return this.allScores;
    }
  },
  methods: {
    onSubmit() {
      this.allScores.push({ empid: this.empid, fname: this.fname, lname: this.lname, age:this.age, gender: this.gender });
      this.clearForm();
    },
    clearForm() {
      this.empid = "";
      this.fname = "";
      this.lname = "";
      this.age = "";
      this.gender = "";
    }
  }
}
</script>

<style>
</style>



